@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oxanium&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oxanium&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}html{scroll-behavior:smooth;height:100%}body{background:url(/landing-page/static/media/bg-1.1b76f9e4.jpg);font-family:"Roboto",sans-serif;color:#eee;min-height:100%;max-width:100%}.section-one{z-index:-1;width:100%;height:100vh;display:flex;flex-direction:column;justify-content:flex-start;align-items:center;background:url(data:image/png;base64,)}.section-two{display:flex;justify-content:space-between;padding:20px}.footer{font-family:"Roboto",sans-serif;background-color:rgba(59,59,59,0.2);height:20vh;display:flex;flex-direction:column;justify-content:center;text-align:center;font-size:1.5rem}.footer .newsletter #email,.footer .newsletter #submit-email{align-self:center;font-family:"Roboto",sans-serif;height:25px;margin:10px 1px;border-style:none;border-radius:5px;font-size:1rem}.footer .newsletter #email{width:200px}.footer .newsletter #submit-email{height:27px;cursor:pointer}#navbar{display:flex;justify-content:space-around;align-items:center;width:100%;font-weight:600;font-size:calc(10px + 2vmin)}#navbar li{display:inline}#navbar a,#navbar a:visited{text-decoration:none;color:#eee;padding:0.9rem 1rem}.company{display:flex;align-items:center;font-family:"Oxanium",sans-serif;font-size:2rem}.company p{margin-top:15px;margin-left:-15px}.company p::after{content:"game on";margin:5px 0 0 25px;display:block;font-size:1.2rem;color:#ffe657}#sidebar{background:rgba(10,10,10,0.5);display:block;margin:10px;width:20vw;border:1px solid #424242;border-top-right-radius:30px;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;padding-bottom:5px}#sidebar header{font-weight:bold;font-size:1.5rem;display:block;text-align:center;margin:5px;padding:10px;color:#eee}#sidebar a{display:block;border-width:1px;margin:5px 0 -5px 0;padding:10px;border-color:#5c5c5c transparent transparent;border-style:solid;color:#eee;text-decoration:none}#sidebar a:hover{background-color:#222;transition:0.5s}#section-one-content{width:100%;height:100vh;display:flex;justify-content:center;align-items:flex-start}#banner{width:30vw;height:auto;margin:2.8rem 0 0 2rem}#main-deals{width:50vw;height:100%;margin:0 2rem;display:flex;flex-direction:column;justify-content:flex-start;align-items:center;text-align:center}#headline{color:#ffe657;width:100%;font-size:3.5rem;text-align:center}.main-sales{width:100%;height:100%;display:flex;justify-content:center;align-items:center}.section-two-deals{width:80vw;border-radius:15px;display:flex;justify-content:space-around;align-items:center;flex-wrap:wrap;margin:10px 30px;padding:30px;background:rgba(59,59,59,0.2);background:rgba(10,10,10,0.5);border:1px solid #424242}.section-two-deals p{text-align:center}.game-card{display:flex;flex-direction:column;justify-content:center;align-items:center;width:200px;height:290px;margin:10px 10px 40px;background:linear-gradient(to bottom, rgba(165,165,165,0.02), rgba(48,48,48,0.2), rgba(165,165,165,0.1));border-radius:5px;border:1px solid #303030;cursor:pointer}.game-card div{width:130px;height:200px;margin:10px}.game-card p{display:inherit;justify-content:center;align-items:center;margin:0 10px 10px;width:75%;height:30px}.game-card .btn{width:75%;height:30px;cursor:pointer;margin:0 10px 10px}.btn{display:flex;justify-content:space-around;align-items:center;font-family:"Roboto",sans-serif;font-size:1.1rem;font-weight:600;border-radius:5px;border:1px solid #e4e4e4;background:#ccc;color:#303030}.btn .fa{color:#3a7a3a}#scroll-top{position:fixed;right:20px;bottom:20px;border-style:none;color:#ffe657;background:transparent}#scroll-top:hover{cursor:pointer}#scroll-top::after{content:"scroll to top";align-self:center;display:block;margin:10px;font-family:"Roboto",sans-serif;font-weight:500}

